import React, { Component } from 'react';
import Page from 'components/LayoutComponents/Page';
import { Row, Col } from 'antd';
import { getChartStaffBySFC, getChartStaffByContractType } from 'services/chart';
import PieChartService from './PieChartService';

class DashboardStaff extends Component {
  state = {};
  render() {
    return (
      <Row gutter={24}>
        <Col span={12}>
          <PieChartService
            chartName="Cán bộ theo loại hợp đồng"
            service={getChartStaffByContractType}
          />
        </Col>
        <Col span={12}>
          <PieChartService chartName="Cán bộ theo ngạch" service={getChartStaffBySFC} />
        </Col>
      </Row>
    );
  }
}

export default props => (
  <Page {...props}>
    <DashboardStaff {...props} />
  </Page>
);



